<template>
    <div class="top">
        <div>
            <van-row type="left" gutter="20" justify="space-between">
                <van-col span="12" style="text-align: left">
                    <span class="blue_line"></span>
                    <span class="plan_title">员工信息</span>
                </van-col>
            </van-row>
        </div>
        <van-form @submit="onSubmit" @failed="failed">
            <van-field v-model="form.userName" name="userName" maxlength="20" label="员工姓名" placeholder="请输入姓名" :rules="[
                { required: true, message: '请输入姓名' },
                { validator: userNameValidator, message: '姓名格式不正确' },
            ]">
            </van-field>

            <van-field v-model="form.idCard" name="idCard" label="员工身份证号码" maxlength="18" placeholder="请输入身份证号码" :rules="[
                { required: true, message: '请输入身份证号' },
                { validator: idCardValidator, message: '身份证号格式不正确' },
            ]" />

            <!-- 手机号码 -->
            <van-field v-model="form.phone" name="phone" label="员工手机号码" placeholder="请输入手机号码" @change="formPhoenChange"
                type="digit" maxlength="11" :rules="[
                    { required: true, message: '请输入手机号码' },
                    { validator: phoneValidator, message: '手机号码格式不正确' },
                ]" />
            <!-- 员工所在地 -->
            <van-field readonly clickable name="ChoosePlan" :value="form.ChoosePlan" label="员工所在地" placeholder=""
                :rules="[{ required: true, message: '请选择' }]" @click="showChoosePlan = true">
                <template #button>
                    <span class="chioce" v-if="!form.ChoosePlan">请选择 </span>
                    <van-icon v-if="!form.ChoosePlan" color="#4ca2fe" style="vertical-align: middle" size="20px"
                        name="arrow" />
                    <van-icon v-if="form.ChoosePlan" style="vertical-align: middle" size="20px" name="arrow" />
                </template>
            </van-field>
            <van-popup v-model="showChoosePlan" position="bottom">
                <van-picker show-toolbar :columns="columnsChoosePlan" @confirm="onConfirmChoosePlan"
                    @cancel="showChoosePlan = false" />
            </van-popup>

            <!-- 具体支行信息 -->
            <van-field v-model="form.phone" v-if="form.ChoosePlan" name="phone" label="" placeholder="请输入具体支行信息"
                @change="formPhoenChange" type="digit" maxlength="11" :rules="[
                    { required: true, message: '请输入具体支行信息' },

                ]" />

            <div style="margin-top: 20px">
                <van-row type="left" gutter="20" justify="space-between">
                    <van-col span="12" style="text-align: left">
                        <span class="blue_line"></span>
                        <span class="plan_title">被保险人信息</span>
                    </van-col>
                </van-row>
            </div>
            <!-- 为谁投保 -->
            <van-field name="bartender" label="为谁投保">
                <template #button>
                    <div class="bartender">
                        <div :class="[
                            form.haveBartender == 1
                                ? 'bartender_div1'
                                : 'bartender_div1_err',
                        ]" @click="haveBartender(1)">
                            配偶
                        </div>
                        <div :class="[
                            form.haveBartender == 2
                                ? 'bartender_div2'
                                : 'bartender_div2_err',
                        ]" @click="haveBartender(2)">
                            子女
                        </div>
                    </div>
                </template>
            </van-field>

            <van-field v-model="form.userName" name="userName" maxlength="20" label="姓名" placeholder="请输入姓名" :rules="[
                { required: true, message: '请输入姓名' },
                { validator: userNameValidator, message: '姓名格式不正确' },
            ]">
            </van-field>
            <!-- 证件类型 -->
            <van-field readonly clickable name="ChoosePlan" :value="form.ChoosePlan" label="证件类型" placeholder=""
                :rules="[{ required: true, message: '请选择' }]" @click="showChoosePlan = true">
                <template #button>
                    <span class="chioce" v-if="!form.ChoosePlan">请选择 </span>
                    <van-icon v-if="!form.ChoosePlan" color="#4ca2fe" style="vertical-align: middle" size="20px"
                        name="arrow" />
                    <van-icon v-if="form.ChoosePlan" style="vertical-align: middle" size="20px" name="arrow" />
                </template>
            </van-field>
            <van-popup v-model="showChoosePlan" position="bottom">
                <van-picker show-toolbar :columns="columnsChoosePlan" @confirm="onConfirmChoosePlan"
                    @cancel="showChoosePlan = false" />
            </van-popup>

            <van-field v-model="form.idCard" name="idCard" label="身份证号" maxlength="18" placeholder="请输入身份证号" :rules="[
                { required: true, message: '请输入身份证号' },
                { validator: idCardValidator, message: '身份证号格式不正确' },
            ]" />

            <van-field label="参保年龄" value="35" disabled>
                <!-- 插槽 -->
                <div slot="label" style="margin-left: 1px" class="flex" @click="clickRightIcon">
                    <span class="filed-label">参保年龄</span>
                    <van-icon name="question" color="#02A7F0" />
                </div>
            </van-field>
            <!--  点击详细信息后面的图标 -->
            <van-popup v-model="clickRightIconValue">
                被保险人参保年龄依照保单生效日 2023 年 1 月 1 日计算，例如：员工配偶
                1982 年 12 月 31 日之前出生，则为 40 周岁及以上；配偶1983 年 1 月 1
                日之后出生，则为 39 周岁及以下。</van-popup>
            <van-field readonly clickable name="dateStartTime" :value="form.dateStartTime" label="证件有效起期" placeholder=""
                @click="DateShowPickerStart = true" :rules="[{ required: true, message: '请选择身份证有效期' }]">
                <!-- 请选择 -->
                <template #button>
                    <span class="chioce" v-if="!form.dateStartTime">请选择 </span>
                    <van-icon v-if="!form.dateStartTime" color="#4ca2fe" style="vertical-align: middle" size="20px"
                        name="arrow" />
                    <van-icon v-if="form.dateStartTime" style="vertical-align: middle" size="20px" name="arrow" />
                </template>
            </van-field>
            <!-- 时间选择器弹框 -->
            <van-popup v-model="DateShowPickerStart" position="bottom">
                <van-datetime-picker v-model="currentDateStart" type="date" title="选择年月日" :min-date="minDate"
                    :max-date="maxDate" @confirm="DateOnConfirm" @cancel="DateShowPickerStart = false" />
            </van-popup>

            <van-field readonly clickable name="dateEndTime" :value="form.dateEndTime" label="证件有效止期" placeholder=""
                @click="DateShowPickerEnd = true" :rules="[{ required: true, message: '请选择身份证有效止期' }]">
                <!-- 点击长期 -->
                <template #button>
                    <div class="dateEndTime">
                        <span class="chioce" v-if="!form.dateEndTime">请选择 </span>
                        <van-icon v-if="!form.dateEndTime" color="#4ca2fe" style="vertical-align: bottom" size="20px"
                            name="arrow" />
                        <van-icon v-if="form.dateEndTime" style="vertical-align: middle" size="20px" name="arrow" />
                        <div class="longTime" @click.stop="longTime">长期</div>
                    </div>
                </template>
            </van-field>
            <!-- 时间选择器弹框 -->
            <van-popup v-model="DateShowPickerEnd" position="bottom">
                <van-datetime-picker v-model="currentDateStart" type="date" title="选择年月日" :min-date="cardDate"
                    :max-date="maxDate" @confirm="DateOnConfirmEnd" @cancel="DateShowPickerEnd = false" />
            </van-popup>
            <!-- 手机号码 -->
            <template v-if="this.judgment">
                <van-field v-model="form.phone" name="phone" label="手机号码" placeholder="请输入手机号码" @change="formPhoenChange"
                    type="digit" maxlength="11" :rules="[
                        { required: true, message: '请输入手机号码' },
                        { validator: phoneValidator, message: '手机号码格式不正确' },
                    ]" />

                <van-field v-model="form.professionalType" name="userName" maxlength="20" label="职业类型" placeholder="请输入职业类型"
                    :rules="[{ required: true, message: '请输入职业类型' }]">
                </van-field>
            </template>
            <van-field name="bartender" label="保障方案">
                <template #button>
                    <div class="bartender" v-if="judgment">
                        <div :class="[
                            form.guarantee == 3 ? 'bartender_div1' : 'bartender_div1_err',
                        ]" @click="haveGuarantee(3)">
                            方案一
                        </div>
                        <div :class="[
                            form.guarantee == 4 ? 'bartender_div2' : 'bartender_div2_err',
                        ]" @click="haveGuarantee(4)">
                            方案二
                        </div>
                    </div>

                    <div class="bartender" v-if="!judgment">
                        <div :class="[
                            form.guarantee == 3 ? 'bartender_div1' : 'bartender_div1_err',
                        ]" @click="haveGuarantee(3)">
                            方案3
                        </div>
                        <div :class="[
                            form.guarantee == 4 ? 'bartender_div2' : 'bartender_div2_err',
                        ]" @click="haveGuarantee(4)">
                            方案4
                        </div>
                    </div>
                </template>
            </van-field>
            <!-- 最下面的价格 -->
            <div class="position-fixed">
                <div class="position-fixed-div">
                    <div class="money_wrap">
                        <div class="money_new">
                            <span>￥ 1688</span> <span>/年</span>
                            <!-- <div style="text-decoration: line-through">
                                原价 元
                            </div> -->
                        </div>
                    </div>
                    <van-button class="insure" native-type="submit">提交</van-button>
                </div>
            </div>
        </van-form>
    </div>
</template>
  
<script>
// 导入中国省市区
import areaList from "../../../../views/card/student-card/views/informationEntry/components/areaList";
import util from "@/components/_utils";
import isIdcard from "../../../../views/card/student-card/views/whiteList/JS/index";
import { Dialog } from "vant";

export default {
    name: "",
    components: {},

    data() {
        return {
            // 安徽项目
            clickRightIconValue: false,
            // 判断是否子女还是配偶
            judgment: true,

            //   日期弹框
            cardDate: new Date(+new Date() + 86400000),
            DateShowPickerStart: false,
            DateShowPickerEnd: false,
            minDate: new Date(1900, 1, 1),
            maxDate: new Date(2100, 1, 1),
            currentDateStart: "",
            // 为谁投保弹框
            showShomInsure: false,
            columnsShowShomInsure: ["父母", "子女"],
            // 选择方案
            showChoosePlan: false,
            columnsChoosePlan: [],
            // 职业类型
            showprofessionalType: false,
            getList: [
                {
                    id: 1,
                    value: "方案一",
                    damage: "10万元",
                    hospital: "10万元",
                    die: "10万元",
                    securityDuring: "12个月",
                    effectiveDate: "2022-12-22",
                },
                {
                    id: 2,
                    value: "方案二",
                    damage: "20万元",
                    hospital: "20万元",
                    die: "20万元",
                    securityDuring: "12个月",
                    effectiveDate: "2022-12-22",
                },
                {
                    id: 3,
                    value: "方案3",
                    damage: "30万元",
                    hospital: "30万元",
                    die: "30万元",
                    securityDuring: "12个月",
                    effectiveDate: "2022-12-22",
                },
            ],
            // 获取选择方案下面的内容
            getListPlan: [],
            // 表单值
            form: {
                whomInsure: "",
                ChoosePlan: "",
                userName: "",
                idCard: "",
                dateStartTime: "",
                dateEndTime: "",
                phone: "",
                haveBartender: "1",
                professionalType: "",
                guarantee: "3",
            },
            InformationFormJSON: JSON.parse(
                sessionStorage.getItem("InformationForm_confirm")
            ),
        };
    },

    created() {
        //   获取当前日期
        let data = new Date();
        let year = data.getFullYear();
        let month = data.getMonth();
        let day = data.getDate();
        // 将现在日期赋值给日期选择器上
        this.currentDateStart = new Date(year, month, day);
    },
    mounted() {
        // 给选择方案赋值
        this.columnsChoosePlan = this.getList.map((e) => {
            return e.value;
        });
    },
    methods: {
        // 点击参保年龄左侧图标时触发
        clickRightIcon() {
            Dialog.alert({
                title: "参保年龄计算规则",
                confirmButtonText: "我知道了",
                message:
                    "被保险人参保年龄依照保单生效日 2023 年 1 月 1 日计算，例如：员工配偶 1982 年 12 月 31 日之前出生，则为 40 周岁及以上；配偶1983 年 1 月 1 日之后出生，则为 39 周岁及以下。",
            }).then(() => {
                // on close
            });
        },
        // 判断为谁投保
        haveBartender(value) {
            this.form.haveBartender = value;
            console.log(" this.form.haveBartender", this.form.haveBartender);

            if (this.form.haveBartender == 1) {
                this.judgment = true;
            } else if (this.form.haveBartender == 2) {
                this.judgment = false;
            }
        },
        // 方案的选择
        haveGuarantee(value) {
            this.form.guarantee = value;
        },
        // 点击长期有效
        longTime() {
            this.form.dateEndTime = "长期有效";
        },
        // 改变电话号码的判断
        formPhoenChange() {
            return this.phoneValidator();
        },
        // 职业类型弹框的确认
        onConfirmprofessionalType(value, index) {
            this.form.professionalType = value;
            this.showprofessionalType = false;
        },
        // 选择身份证弹框中确认
        DateOnConfirmEnd(time) {
            let setMonth = time.getMonth() + 1;
            if (setMonth >= 1 && setMonth <= 9) {
                setMonth = "0" + setMonth;
            }
            let day = time.getDate();
            if (day < 10) {
                day = "0" + day;
            }
            this.form.dateEndTime = `${time.getFullYear()}-${setMonth}-${day}`;
            this.DateShowPickerEnd = false;
        },
        DateOnConfirm(time) {
            let setMonth = time.getMonth() + 1;
            if (setMonth >= 1 && setMonth <= 9) {
                setMonth = "0" + setMonth;
            }
            let day = time.getDate();
            if (day < 10) {
                day = "0" + day;
            }
            this.form.dateStartTime = `${time.getFullYear()}-${setMonth}-${day}`;
            this.DateShowPickerStart = false;
        },
        // 姓名校验
        userNameValidator() {
            return util.checkName(this.form.userName);
        },
        // 身份证格式的验证
        idCardValidator() {
            return isIdcard(this.form.idCard);
        },
        // 手机号格式的验证
        phoneValidator() {
            return /^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(this.form.phone);
        },
        // 提交失败
        failed(values) {
            this.$toast(values.errors[0].message);
            console.log("va", values);
        },
        // 表单确认
        onSubmit(values) {
            console.log("submit", values);
            console.log("this。form", this.form);
            this.$router.push({
                path: "/anhui/successPage",
            });

        },
        // 点击为谁投保的确认
        onConfirm(value, index) {
            console.log("选择为谁投保", value, index);
            // 给为谁投保后面赋值
            this.form.whomInsure = value;
            this.showShomInsure = false;
        },
        // 选择方案
        onConfirmChoosePlan(value, index) {
            console.log("选择方案", value, index);
            this.form.ChoosePlan = value;
            this.showChoosePlan = false;
        },
    },
    watch: {},
    computed: {},
};
</script>
  
<style lang='less' scoped>
.top {
    margin-top: 5vh;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
}

.plan_title {
    font-size: 18px;
    font-family: PingFang SC-Medium, PingFang SC;
    font-weight: 600;
    color: #303133;
}

.blue_line {
    display: inline-block;
    width: 6px;
    height: 3vh;
    background: #4ca2fe;
    border-radius: 0px 0px 0px 0px;
    opacity: 1;
    margin-right: 5px;
    vertical-align: bottom;
}

.underwriter {
    text-align: left;
    display: flex;
    flex-direction: column;
    padding: 1vh;
    margin-top: 3vh;
    background: #ffffff;
    box-shadow: 0px 0px 6px 1px rgba(159, 159, 159, 0.16);
    border-radius: 8px 8px 8px 8px;
    opacity: 1;
}

