    flex-direction: column;
    background: #ffffff;
    box-shadow: 0px 0px 6px 1px rgba(159, 159, 159, 0.16);
    border-radius: 8px 8px 8px 8px;
    opacity: 1;
}

.planClass_div {
    margin-top: 2vh;
    display: flex;
    justify-content: space-between;
}

/deep/ .van-field__label {
    padding-left: 1vh;

    //   font-weight: 550;
    width: 120px;
}

.dateEndTime {
    display: flex;
    justify-content: center;
    align-items: center;
}

.longTime {
    color: #4ca2fe;
    border-radius: 5px;
    border: #4ca2fe 1px solid;
    padding: 2px;
    width: 50px;
    height: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: small;
}

