    border-radius: 14px 14px 14px 14px;
    opacity: 1;
    border: 1px solid #9ca1a6;
    // color: #4ca2fe;
    margin-right: 1vh;
}

.bartender_div2_err {
    margin-left: 1vh;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 68px;
    height: 28px;
    border: 1px solid #d6d6d6;
    border-radius: 14px 14px 14px 14px;
    opacity: 1;
}

.bartender_div2 {
    position: relative;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 68px;
    height: 28px;
    background: #4ca2fe;
    border-radius: 14px 14px 14px 14px;
    opacity: 1;
    border: 1px solid #4ca2fe;

    img {
        position: absolute;
        top: 0;
        right: 0;
        width: 20px;
        height: 20px;
    }
}

.position-fixed {
    // position: fixed;
    margin: 15px 0;

    bottom: 0;
